module State

open Types
open Api
open Fable.Import.Browser
open Elmish


// State
let init model =
  let model = {
    ModuleId      = (document.getElementById Types.containerId).dataset.["moduleid"]
    CurrentUser   = None
    ErrorMessages = []
  }
  model, Api(model.ModuleId).FetchCurrentUser

let update  msg model=
  let api = Api(model.ModuleId)
  match msg with
  | UserFetched p  ->  {model with CurrentUser = p}, Cmd.none
  | FetchFailed  e ->  {model with ErrorMessages = e.Message :: model.ErrorMessages}, Cmd.none


